<template>
	<view class="footer">
		<view class="showTitle">
			<view class="vrshow" v-for="(item,index) in boottomList" :key='index'>
				<view class="">
					<text class="iconfont" :class="item.icon"></text>
				</view>
				<text>
					{{item.title}}
				</text>
			</view>
		</view>
		<view class="footerBtn">
			<view class="threeShow" @click="Jsshow">
				三维显示
			</view>
			<view class="selectShow" @click="confirmSelect">
				选择三维图形
			</view>
		</view>
		<!-- popUp -->
		<view class="popUpBox">
			<uni-popup id="popupShare" ref="popupShare" type="share" @change="change">
				<view class="poplist">
					<view class="pop_header">
						<uni-segmented-control :current="current" :values="items" @clickItem="onClickItem" style-type='text' activeColor="#86B0D4"></uni-segmented-control>
					</view>
					<view class="content">
						 <view>
							<scroll-view scroll-y="true" class="classifyItem">
								<view class="content_item" v-for="(item,index) in dataList" :key='index'>
									<text @click="changePic(index,item.id)" :class="index==boolIndex?'textColor':''">{{item.title}}</text>
								</view>
							</scroll-view>
						</view>
					</view>
				</view>
			</uni-popup>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				// 底部导航
				boottomList:[
					{icon:'iconzhuanfa',title:'转发'},
					{icon:'iconshebeiguanli',title:'VR显示'},
				],
				current:0,
				items: ['三维文件'],
				boolIndex:0,//底部选中的index
			}
		},
		props:{
			// 顶部menu的index
			Bottomcurrent:{
				type:Number,
				default:''
			},
			dataList:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			stlId:{
				type:String,
				default:''
			}
		},
		methods:{
			onClickItem(e) {
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex;
				}
			},
			change(e){},
			// 选中底部menu,切换图片与标题
			changePic(index,id){
				this.boolIndex=index
				console.log(id)
				var modeldata=this.dataList.filter((item)=>{
					if(id==item.id){
						return item
					}
				})
				console.log(modeldata)
				this.bus.$emit('electricData',modeldata[0])//改变轮播图父组件的值
			},
			// 三维显示
			Jsshow(){
				console.log(this.stlId)
				if(!this.stlId){
					uni.showToast({
						title:'无相关文件',
						icon:'none'
					})
					return;
				}
				var baseUrl='https://technology.shoyii.com/?cos'+'='+true+'&userType=D'+'&token='+'&modelType='+'&titlename='+'&fileId='+this.stlId
				var url=encodeURIComponent(baseUrl)
				uni.navigateTo({
					url:'/pages/ThreejsShow/ThreejsShow?baseUrl='+url,
					animationType:'pop-in'
				})
			},
			// 选择三维图形
			confirmSelect(){
				console.log(this.Bottomcurrent)
				console.log(this.dataList)
				if(this.Bottomcurrent==0){//电子病例
					this.$refs.popupShare.open()
				}
			}
		},
		created() {
		}
	}
</script>

<style scoped>
	.footer{
		height:110rpx;
		border-top:1px solid #EEEEEE;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 30rpx;
		width: 100%;
		/* background: #FFFFFF; */
		background-color: red;
	}
	.showTitle{
		width: 45%;
		display: flex;
		height: 80rpx;
	}
	.vrshow{
		color: #000000;
		font-size: 20rpx;
		text-align: center;
		width: 130rpx;
		font-family: PingFang-SC-Medium;
		font-weight: 500;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.vrshow .iconfont{
		font-size: 36rpx;
	}
	.footerBtn{
		width: 100%;
		height: 80rpx;
		display: flex;
		justify-content: space-between;
		line-height: 80rpx;
		margin-left: 20rpx;
		flex: 1;
	}
	.footerBtn uni-view{
		flex: 1;
		border: 1px solid #86B0D4;
		color: #86B0D4;
		text-align: center;
		font-size: 24rpx;
		font-family: PingFang-SC-Medium;
		font-weight: 500;
	}
	.threeShow{
		border-top-left-radius: 40rpx;
		border-bottom-left-radius: 40rpx;
	}
	.selectShow{
		border-top-right-radius: 40rpx;
		border-bottom-right-radius: 40rpx;
		background-color: #86B0D4;
		color: #FFFFFF!important;
	}
	.poplist{
		padding: 0 30rpx;
		height: 715rpx;
		background: #FFFFFF;
		/* display: flex;
		flex-direction: column; */
	}
	.poplist .content{
		color:#333333;
		font-size: 30rpx;
	}
	.poplist .content_item{
		border-bottom: 1px solid #EEEEEE;
		text-indent: 10rpx;
		padding: 25rpx 0;
	}
	.poplist .content_item uni-text{
		display: block;
	}
	.pop_header{
		border-bottom: 1px solid #EEEEEE;
		padding: 10rpx 0;
	}
	.pop_header /deep/ .segmented-control__item--text{
		border-bottom-width: 0;
	}
	.classifyItem{
		/* flex: 1; */
		height: 610rpx;
		/* overflow: auto; */
	}
	.textColor{
		color: #86B0D4;
	}
</style>
